/** CALENDAR **/

$primary-colour: #5dc145;

$body-bold: bold;
$ease: ease-in-out;

$grey-6: #333;
$grey-5: #4f4f4f;
$grey-4: #828282;
$grey-3: #BDBDBD;
$grey-2: #E0E0E0;
$grey-1: #F2F2F2;

@mixin ratio($width, $height) {
  &::before {
    content: '';
    display: block;
    padding-top: calc((#{$height} / #{$width}) * 100%);
  }
}

.calendar {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 0 0;
  max-width: 800px;
  .day {
    margin: 0 1px 1px 0;
    width: calc(14.2857142857% - 7px);
    background: $grey-1;
    color: $grey-3;
    padding: 30px 5px 5px;
    display: flex;
    align-items: flex-end;
    @include ratio(3, 2);
    position: relative;
    font-size: .85em;
    line-height: 1.3;
    .date {
      position: absolute;
      top: 5px;
      right: 10px;
      font-weight: $body-bold;
      font-size: 1.25em;
    }
    &.blank {
      background: none;
      border: 1px solid $grey-1;
    }
    &.course {
      border-color: darken($primary-colour, 5%);
      background: $primary-colour;
      color: #fff;
      transition: background .2s $ease;
      &:hover {
        background: lighten($primary-colour, 5%);
      }
    }
    &.day-name {
      padding: 0;
      font-size: .8em;
      text-align: center;
      background: none;
      border: 0;
      color: $grey-5;
      padding-bottom: 15px;
      justify-content: center;
      &::before {
        display: none;
      }
    }
  }
}

.calendar-details {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  h2 {
    padding: 0 20px;
    color: $grey-5;
    margin-bottom: 0;
  }
  a {
    font-weight: $body-bold;
    font-size: 1.6em;
  }
}

.calendar-columns {
  display: flex;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 100%;
  .left {
    margin-right: 50px;
  }
  .left,
  .right {
    flex: 1;
  }
  table {
    width: 100%;
  }
  thead {
    font-weight: $body-bold;
  }
  tr,
  td {
    background: none;
    border: 0;
    padding: 5px 0;
  }
}

/** END CALENDAR **/

/** UPCOMING EVENTS **/

.formation-event-upcoming-events {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  padding: 0;
  .formation-event {
    display: flex;
    margin-bottom: 10px;
  }
  .formation-event-date {
    width: 75px;
    background: $primary-colour;
    padding: 5px 5px 0;
    text-transform: uppercase;
  }
  .formation-event-day {
    background: rgba(255, 255, 255, .9);
    color: $primary-colour;
    padding: 3px;
    text-align: center;
    letter-spacing: .5px;
  }
  .formation-event-month {
    font-size: 1.6em;
    line-height: 1.4;
    font-weight: bold;
    color: #fff;
    text-align: center;
  }
  .formation-event-details {
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .formation-event-name {
    font-size: 1.1em;
    font-weight: bold;
    margin-bottom: 5px;
  }
}

/** END UPCOMING EVENTS **/
